import React from 'react';
import { Panel, PanelHeader, HeaderButton, Gallery, Button, Div, Group, platform, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

class Task extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
            slideIndex: 0,
            imageHeight : 350
        }
	}

	render() {
		let { id, task, go, done } = this.props
		const osname = platform();
		return (
			<Panel id={id}>
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="tasks">
						{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</HeaderButton>}
				>
					Задание
				</PanelHeader>
                <Group title="Название задания"><Div><b>{task.name}</b></Div></Group>
                <Group title="Вознаграждение"><Div><b>{task.price}</b> баллов, зачислятся за <b>{task.reason}</b></Div></Group>
                <Group title="Суть задания">
                        <Div>
                            {task.description}
                        </Div>
                </Group>
				{ 
                        task.photos.length > 0 && 
                        (
                            <Group title="Фотографии">
                                <Gallery
                                    slideWidth="100%"
                                    style={{ height: this.state.imageHeight, textAlign : 'center' }}
                                    bullets="dark"
                                >
                                    { 
                                        <div style={{width: '100%', height : '100%', backgroundColor : '#fff'}}>
                                            <img 
                                                alt=""
                                                style={{  
                                                    maxHeight : this.state.imageHeight
                                                }} src={task.photos}>
                                            </img>
                                        </div>
                                    }
                                </Gallery>
                            </Group>
                        )
                    }
                    {
                        task.photos.length === 0 &&
                        (
                            <Group title="Фотографии">
                                <Div>Фотографий нет</Div>
                            </Group>
                        )
                    }
                    {
                        done === true &&
                            <Div>
                                <Button size="xl" level="secondary">Выполнено</Button>
                            </Div>                        
                    }
                    {
                        task.report === "1" && done === false &&
                            <Div>
                                <Button size="xl" level="commerce">Я выполнил</Button>
                            </Div>                        
                    }
                    {
                        task.report === "0" && done === false &&
                            <Div>
                                <Button size="xl" level="secondary">Автоматически</Button>
                            </Div>                        
                    }

			</Panel>
		);
	}
}

export default Task;