import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Epic, Tabbar, TabbarItem, Alert } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Similar from '@vkontakte/icons/dist/24/similar';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

import Home from './panels/Home';
import Top from './panels/Top';
import Tasks from './panels/Tasks';
import Task from './panels/Task';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activeStory: 'index',
			activePanel: 'tasks',
			http_web_site: 'https://pso31.ru/active_pso/vkapp/',
			authToken: null,
			topDataTopPlace: '0',
			topFriendsDataTopPlace: '0',
			tasksCount: '0',
			userData: null,
			userMember: false,
			topData: null,
			topFriendsData: null,
			tasksData: null,
			photoState: true,
			dataState: true,
			topState: true,
			topFriendsState: true,
			tasksState: true,
			taskDone: false,
			fetchedUser: null,
			popout: null
		};
		this.onStoryChange = this.onStoryChange.bind(this);
		this.closePopout = this.closePopout.bind(this);
		this.openPopout = this.openPopout.bind(this);
	}

	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story })
	  }	

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });

					//Получаем у каждого фотографию и заносим в таблицу

					fetch(this.state.http_web_site + "/api/method/users.setParams.php?user_id=" + this.state.fetchedUser.id + "&photo=" + this.state.fetchedUser.photo_200 + "&first_name=" + this.state.fetchedUser.first_name + "&last_name=" + this.state.fetchedUser.last_name)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ photoState: false }),
						console.log(data.response)
					))

					//Получаем общий ТОП

					fetch(this.state.http_web_site + "/api/method/app.getTop.php?limit=50&user_id=" + this.state.fetchedUser.id)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ topState: false }),
						this.setState({ topData: data.response }),
						this.setState({ topDataTopPlace: data.top_place }),
						console.log(data)
					))
					break;
				case 'VKWebAppAccessTokenReceived':
					this.setState({ authToken: e.detail.data.access_token });
					this.getTasks();

					connect.send("VKWebAppCallAPIMethod", {"method": "groups.isMember", "params": {"group_id": "137516675", "user_id": this.state.fetchedUser.id, "v":"5.95","access_token": this.state.authToken}, "request_id": "is_member"});

					// Получаем объект из таблицы

					fetch(this.state.http_web_site + "/api/method/users.Get.php?user_id=" + this.state.fetchedUser.id)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ userData: data.response }),
						this.setState({ dataState: false })
					))


					//Получаем общий ТОП друзей

					fetch(this.state.http_web_site + "/api/method/app.getFriendsTop.php?limit=50&user_id=" + this.state.fetchedUser.id + "&access_token=" + this.state.authToken)
					.then((res) => res.json())
					.then((data) => (
						this.setState({ topFriendsState: false }),
						this.setState({ topFriendsData: data.response }),
						this.setState({ topFriendsDataTopPlace: data.top_place }),
						console.log(data)
					))
					break;
				case 'VKWebAppCallAPIMethodResult':
					if (e.detail.data.request_id === "is_member")
					{
						if(e.detail.data.response !== 1)
						{
							this.openPopout();
						}	
					}
					break;
				case 'VKWebAppJoinGroupResult':
					if(e.detail.data.result === true)
					{
						this.closePopout();
					}
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 6966408, "scope": "friends, wall, groups"});
	}

	getInfoTask(id) {
		fetch(this.state.http_web_site + "/api/method/tasks.Get.php?task_id=" + id + "&user_id=" + this.state.fetchedUser.id)
		.then((res) => res.json())
		.then((data) => (
			this.setState({ taskData: data.response }),
			console.log(data),
			this.setState({ activePanel: "task" }),
			this.setState({ taskDone: data.done })
		))
	}

	getTasks() {
		//Получаем задания

		fetch(this.state.http_web_site + "/api/method/app.getTasks.php")
		.then((res) => res.json())
		.then((data) => (
			this.setState({ tasksState: false }),
			this.setState({ tasksData: data.response }),
			this.setState({ tasksCount: data.count }),
			console.log(data)
		))
	}

	subscribe() {
		connect.send("VKWebAppJoinGroup", {"group_id": 137516675});
	}

	openPopout = () => 
	{
		this.setState({ popout: null });
		this.setState({ popout:
		  <Alert
			actions={[{
			  title: 'Подписаться',
			  autoclose: false,
			  style: 'default',
			  action: () => this.subscribe()
			},
			{
				title: 'Закрыть приложение',
				autoclose: false,
				style: 'destructive',
				action: () => connect.send("VKWebAppClose", {"status": "success"})
			  }]}
			onClose={this.openPopout}
		  >
			<p>Чтобы пользоваться приложением, Вам необходимо подписаться:</p>
		  </Alert>
		});
	}
	
	closePopout = () => {
		this.setState({ popout: null });
	}

	go = (e, object) => {
		let activePanel = e.currentTarget.dataset.to
		if (activePanel === 'tasks') {
			connect.send("VKWebAppSetLocation", {
				"location": "tasks"
			});
			this.getTasks();
			this.setState({ activePanel: "tasks" })
		}
		if (activePanel === 'task') {
			connect.send("VKWebAppSetLocation", {
				"location": `task${object.taskId}`
			});
			this.getInfoTask(object.taskId);
		}
	}

	render() {
			return (
				<Epic activeStory={this.state.activeStory} tabbar={
					<Tabbar>
					  <TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'index'}
						data-story="index"
						text="Главная"
					  ><Icon24Home /></TabbarItem>
					  <TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'tasks'}
						data-story="tasks"
						label={this.state.tasksCount}
						text="Задания"
					  ><Icon24Similar /></TabbarItem>
					  <TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'top'}
						data-story="top"
						text="Топ"
					  ><Icon24Favorite /></TabbarItem>
					</Tabbar>
				  }>
					<View id="index" activePanel="home" popout={this.state.popout}>
						<Home id="home" fetchedUser={this.state.fetchedUser} dataUser={this.state.userData} spinner={this.state.dataState}/>
					</View>
					<View id="tasks" activePanel={this.state.activePanel} popout={this.state.popout}>
						<Tasks id="tasks" go={this.go} dataUser={this.state.userData} tasks={this.state.tasksData} spinner={this.state.tasksState} count={this.state.tasksCount} />
						<Task id="task" task={this.state.taskData} go={this.go} done={this.state.taskDone}/>
					</View>
					<View id="top" activePanel="top" popout={this.state.popout}>
						<Top id="top" 
						dataUser={this.state.userData} 
						topAll={this.state.topData} 
						spinnerAll={this.state.topState} 
						placeAll={this.state.topDataTopPlace}
						topFriends={this.state.topFriendsData} 
						spinnerFriends={this.state.topFriendsState} 
						placeFriends={this.state.topFriendsDataTopPlace} />
					</View>
				  </Epic>
			);
	}
}

export default App;
