import React from 'react';
import { Panel, Avatar, PanelHeader, List, Cell, Spinner, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui';
import './style.css';


class Top extends React.Component {
	constructor(props) {
        super(props);

		this.state = {
            activeTab: "all"
        }
    }
    
    render() {
    let { id, spinnerAll, topAll, dataUser, placeAll, topFriends, placeFriends, spinnerFriends } = this.props

        return (
            <Panel id={id}>
                <PanelHeader>Топ пользователей</PanelHeader>

                <Tabs theme="header">
                  <TabsItem
                    onClick={() => this.setState({ activeTab: 'all' })}
                    selected={this.state.activeTab === 'all'}
                  >
                    Все
                  </TabsItem>
                  <TabsItem
                    onClick={() => this.setState({ activeTab: 'friends' })}
                    selected={this.state.activeTab === 'friends'}
                  >
                    Друзья
                  </TabsItem>
                </Tabs>

                {spinnerAll &&
                <div style={{display: 'flex', alignItems: 'center', flexDirection: 'colomn'}}>
                    <Spinner size="large" style={{ marginTop: 20 }}/>
                </div>}
                {spinnerFriends &&
                <div style={{display: 'flex', alignItems: 'center', flexDirection: 'colomn'}}>
                    <Spinner size="large" style={{ marginTop: 20 }}/>
                </div>}

                {this.state.activeTab === "all" &&
                
                <List>
                    {topAll.length > 0 && topAll.map((item, index) => (
                    <Cell
                        data-top-id={item.id}
                        indicator={index + 1}
                        href={'//vk.com/id' + item.user_id}
                        key={index}
                        title="Король лайков"
                        description={+item.p_likes + +item.p_flikes + +item.p_tlikes + +item.p_alllikes + +item.p_comm + +item.p_gposts + +item.p_gcomm + " баллов"}
                        before={item.photo ? <Avatar src={item.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                        {item.username ? item.username : 'Unknown'}
                    </Cell>))}
                
                    <FixedLayout vertical="bottom">
                        <Cell
                            className="fixed-top-account"
                            description={+dataUser.p_likes + +dataUser.p_flikes + +dataUser.p_tlikes + +dataUser.p_alllikes + +dataUser.p_comm + +dataUser.p_gposts + +dataUser.p_gcomm + " баллов"}
                            indicator={"~" + placeAll}
                            key={topAll.top_place}
                            before={dataUser.photo ? <Avatar src={dataUser.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                            {dataUser.username ? dataUser.username : 'Unknown'}
                        </Cell>
                    </FixedLayout>
                </List>
                }
            
            {this.state.activeTab === "friends" &&

            <List>
                {topFriends.length > 0 && topFriends.map((item, index) => (
                <Cell
                    data-top-id={item.id}
                    indicator={index + 1}
                    href={'//vk.com/id' + item.user_id}
                    key={index}
                    title="Король лайков"
                    description={+item.p_likes + +item.p_flikes + +item.p_tlikes + +item.p_alllikes + +item.p_comm + +item.p_gposts + +item.p_gcomm + " баллов"}
                    before={item.photo ? <Avatar src={item.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                    {item.username ? item.username : 'Unknown'}
                </Cell>))}

                <FixedLayout vertical="bottom">
                    <Cell
                        className="fixed-top-account"
                        description={+dataUser.p_likes + +dataUser.p_flikes + +dataUser.p_tlikes + +dataUser.p_alllikes + +dataUser.p_comm + +dataUser.p_gposts + +dataUser.p_gcomm + " баллов"}
                        indicator={"~" + placeFriends}
                        key={topFriends.top_place}
                        before={dataUser.photo ? <Avatar src={dataUser.photo}/> : <Avatar src='https://vk.com/images/camera_100.png'/>}>
                        {dataUser.username ? dataUser.username : 'Unknown'}
                    </Cell>
                </FixedLayout>
            </List>
            }
            </Panel>
        );
    }
}
export default Top;